import request from '@/utils/request'

// 教师登录
export function login(data) {
    return request({
        url: '/teacher/login',
        method: 'post',
        params: {},
        data: data,
    })
}
// 教师获取信息
export function getByToken() {
    return request({
        url: '/teacher/getByToken',
        method: 'get',
        params:{},
        data:{}
    })
}

