<template>
    <div>
        <el-card class="header">
            <el-form :inline="true" class="headerForm" :model="searchHeaderForm">
                <el-form-item><el-input clearable v-model="searchHeaderForm.find" placeholder="请输入名称"></el-input>
                </el-form-item>
                <el-form-item label="课程类型">
                    <el-select v-model="searchHeaderForm.teacher_id" clearable placeholder="请选择课程类型">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-button type="primary" @click="getDatas">查询</el-button>
                <el-button class="searchBtn" type="primary" @click="handleData('add')">添 加</el-button>
            </el-form>
        </el-card>
        <el-card>
            <el-table :data="tableData" border style="width: 100%"
                :header-cell-style="{ background: '#F3F4F7', color: '#555' }">
                <el-table-column prop="name" label="课程名称" min-width="200">
                </el-table-column>
                <el-table-column prop="department" label="院系" min-width="200">
                </el-table-column>
                <el-table-column prop="grade" label="年级" min-width="200">
                </el-table-column>
                <el-table-column prop="create_time" label="创建时间" min-width="200">
                </el-table-column>
                <el-table-column align="center" fixed="right" label="操作" width="200">
                    <template #default="scope">
                        <el-button type="primary" @click="showCourseDetails(scope.row._id)">详情</el-button>
                        <!-- <el-button type="danger" @click="deleteData(scope.row._id)">删除</el-button> -->
                    </template>
                </el-table-column>
            </el-table>
            <div v-if="tableData.length !== 0" style="display: flex; flex-direction: row-reverse; margin-top: 10px">
                <el-pagination :current-page="currentPage" :page-size="pageSize" :total="total"
                    :page-sizes="[10, 20, 50, 100]" background layout="total, sizes, prev, pager, next, jumper"
                    @size-change="handleSizeChange" @current-change="handleCurrentChange" />
            </div>
        </el-card>
        <el-dialog :modal-append-to-body="false" v-model="operaDialog" :title="dialogTitle" @close="resetForm">
            <el-form status-icon :model="operaForm" ref="operaForm" class="operaDialog" label-width="100px" :rules="rules">
                <el-form-item label="课程名称" prop="name">
                    <el-input v-model="operaForm.name" />
                </el-form-item>
                <el-form-item label="学科" prop="department">
                    <el-input v-model="operaForm.department" />
                </el-form-item>
                <el-form-item label="年级" prop="grade">
                    <el-input v-model="operaForm.grade" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm()">提交</el-button>
                    <el-button @click="resetForm">取消</el-button>
                </el-form-item>
            </el-form>

        </el-dialog>
    </div>
</template>
<script>
import {
    course_create,
    course_delete,
    course_update,
    course_find,
} from '@/api/teacher/api.js'

// eslint-disable-next-line no-unused-vars
export default {
    data() {



        return {

            tableData: [],
            // 控制收缩
            isCollapse: false,

            search: "",
            operaDialog: false,
            dialogTitle: "",
            pageSize: 10, // 分页的页面大小
            total: 0, // 分页的数据总数
            currentPage: 1, // 分页的当前页
            opera: "",
            operaForm: {},
            //查询
            searchHeaderForm: {
                find: '',
                teacher_id: "",
            },
            options: [
                {
                    value: '',
                    label: '全部'
                },
                {
                    value: sessionStorage.getItem("user_id"),
                    label: '仅自己'
                }
            ],

        }
    },
    created() {
        this.getDatas()
        this.getMsg()
    },
    methods: {
        desearch() {
            this.searchHeaderForm = {
                find: '',
            },
                this.getDatas()
        },
        handleData(handle, row) {
            if (handle == "add") {
                this.operaForm = {}
                this.dialogTitle = "添加API"
                this.opera = "add"
            } else if (handle == "update") {
                this.operaForm = { ...row } //!!! 直接赋值是引用类型,引起表单重置问题
                this.dialogTitle = "编辑API"
                this.opera = "update"
            }
            this.operaDialog = true
        },
        handleSizeChange(value) {
            // 改变页面大小
            this.pageSize = value
            this.getDatas()
        },
        handleCurrentChange(value) {
            // 改变页数
            this.currentPage = value
            this.getDatas()
        },
        async getDatas() {

            const params = {
                page: this.currentPage,
                page_size: this.pageSize,
                search: this.searchHeaderForm.find,
                teacher_id: this.searchHeaderForm.teacher_id,
            }
            const res = await course_find(params)
            if (res.data.code === 200) {
                if (res.data.data) {
                    this.total = res.data.data.count
                    if (res.data.data.data == null) {
                        this.tableData = []
                    } else {
                        this.tableData = res.data.data.data
                    }
                } else {
                    this.total = res.data.data.count
                    this.tableData = []
                }
            }
        },
        async getMsg() {
            setTimeout(() => {
                this.options[1].value = sessionStorage.getItem("user_id");
            }, 100);

        },
        addData() {
            course_create(this.operaForm).then((res) => {
                console.log(res);
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: "创建成功",
                        type: "success",
                    })
                    this.operaDialog = false
                    this.getDatas()
                }
            })
        },
        deleteData(data) {
            course_delete({ _id: data }).then((res) => {
                console.log(data);
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: "删除成功",
                        type: "success",
                    })
                    this.operaDialog = false
                    this.getDatas()
                }
            })
        },
        editData() {
            course_update(this.operaForm).then((res) => {
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: res.data.msg,
                        type: "success",
                    })
                    this.operaDialog = false
                    this.getDatas()
                }
            })
        },
        showCourseDetails(data) {
            this.$router.push({
                path: '/teacher/courseDetails',
                query: { course_id: data }
            })

        },
        submitForm() {
            this.$refs.operaForm.validate((valid) => {
                if (valid) {
                    if (this.opera === "add") {
                        this.addData()
                    } else {
                        this.editData()
                    }
                } else {
                    this.$message({
                        showClose: true,
                        message: "请根据要求将信息填写完整",
                        type: "warning",
                    })
                }
            })
        },
        resetForm() {
            this.$refs.operaForm.clearValidate()
            this.operaForm = {},
                this.operaDialog = false
        },
    },
};
</script>
  
<style lang="less" scoped>
.header {
    margin-bottom: 20px;

    .searchBtn {
        float: right;
    }

    .headerForm {
        padding: 0 !important;

        .el-form-item {
            margin-bottom: 0px;
        }
    }
}

.upload-demo {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
}

.el-date-editor.el-input {
    width: 100%;
}

.fileLink {
    color: #f8f8f8;
    text-decoration: none;
}

.el-form {
    padding: 0 100px;
}

/deep/ .operaDialog .el-form-item__label {
    padding: 0 25px 0 0;
}
</style>
  